@import url("./ultils/reset.css");

.root {
    --app-background: rgb();
}

.container {
    width: min(100% - 30px, 1080px);
    margin-inline: auto;
}

main {
    min-height: 100dvh;
    display: grid;
    place-items: center;
}

.star {
    cursor: pointer;
    padding: 2px;
}

.grey {
    color: #444;
    font-size: 5rem;
}

.gold {
    color: gold;
    font-size: 5rem;
}

.flex {
    display: flex;
}

h2 {
    text-align: center;
    color: #fff;
    font-size: 25px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-top: 15px;
}

button {
    position: fixed;
    bottom: 50px;
    right: 50px;
    width: 70px;
    line-height: 42px;
    font-size: 1.8rem;
    font-weight: 600;
    border-radius: 6px;
    padding-bottom: 1px;
    color: #fff;
    background-color: rgb(15, 113, 255);
    transition: all ease-in-out 150ms;
}

.dialog button {
    position: relative;
    bottom: 0;
    left: 0;
    width: 70px;
    line-height: 42px;
    font-size: 1.8rem;
    font-weight: 600;
    border-radius: 6px;
    padding-bottom: 1px;
    color: #fff;
    background-color: rgb(255, 33, 13);
    transition: all ease-in-out 150ms;
}

button:hover {
    background-color: rgba(15, 111, 255, 0.9);
}

.dialog button:hover {
    background-color: rgba(255, 33, 13, 0.8);
}

.dialog {
    background-color: #333;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 210px;
    width: 300px;
    display: grid;
    place-items: center;
    border-radius: 10px;
}

.dialog p {
    margin: 0 0 30px 0;
    font-size: 25px;
    font-weight: 500;
    font-family: sans-serif;
    color: #fff;
}

.dialog p::after{
    content: ":)";
    display: block;
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(90deg);
    color: #fff;
    font-size: 30px;
    font-weight: 600;
    border-radius: 50px;
    width: 45px;
    height: 48px;
    text-align: center;
    background-color: rgb(15, 113, 255);
}

